.info {
    width: 50%;
    margin-left: 25%;
    text-align: left;
}


.volunteer input[type=text], input[type=password] {
    width: 100%;
    padding: 12px 20px;
    margin: 8px 0;
    display: inline-block;
    border: 1px solid #ccc;
    box-sizing: border-box;
}

.volunteer button {
    background-color: #62008F;
    color: white;
    padding: 14px 20px;
    margin: 8px 0;
    border: none;
    cursor: pointer;
    width: 100%;
}

.volunteer button:hover {
    opacity: 0.8;
}